body{
	background-color: #2C3237;
	
}
.flex-grid {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;	
	-ms-flex-pack: center;	
	    justify-content: center;	
	-ms-flex-align: center;	
	    align-items: center;
}
.col {
	-ms-flex: 1 1;
	    flex: 1 1;
}
.column {
	float: left;
	width: 33.33%;
	text-align: left;
}

.row:after {
	content: "";
	display: table;
	clear: both;
}

.info, .map{
	padding: 0px;
	text-align: justify;
}

.row{
	width: 100%;
	margin: 0px;
}
.info p, h2{
	margin: 5px 10px 5px 10px;
}
p{
	font-size:20px;
}

h3{
	font-size: 35px;
}

h4{
	font-size: 25px;
	-webkit-transition: .5s ease;
	-o-transition: .5s ease;
	transition: .5s ease; 
}
h4:hover{
	color: #D3D3D3;
	font-size: 26px;
}

.inline{
	display: inline-block;
}

.btn{
	margin: 15px;
	background: #00B6A2;
	border: 0;
	color: white;
	text-transform: uppercase;
	border-radius: 0px;
	font-weight: bold;
	padding: 12px 20px 12px 20px;

	-webkit-transition: .5s ease;

	-o-transition: .5s ease;

	transition: .5s ease;   
}

.btn:hover{
	color: black;
}


.contactData{
	margin: 20px;
	text-align: left; 
}
.contactData h4{
	font-size: 16px;
}
.contactData .btn{
	margin: 5px;
}


